import React from "react";

import "react-sliding-pane/dist/react-sliding-pane.css";
import Grid from "@material-ui/core/Grid";
import LOGO from "../LOGO.png";
import Modal from "./Modal";
function HeroSection(props) {
  const [showModal, setShowModal] = React.useState(false);

  return (
    <div className="o-hidden">
      <Grid container>
      <Grid item xs={12}>
      <img
            className="heroimg"
            src="https://i.ibb.co/vqgqXDT/New-Project-4.png"
            alt=""
          />
      </Grid>
        <Grid item xs={6}>
          <Grid container justifyContent="center">
            <img src={LOGO} alt="logo" className="logo" />
            <div className="textStyle">
            <h1 >
             Our website is 
              <span className="">
                 under construction
              </span>
            </h1>
            <p className="">
              Sutharwala is Gujarat's largest home services marketplace the
              platform helps customers to book reliable home/offices and other
              sectors services like service of carpenter, furniture, fixture
              repairing ,interior designing turnkey projects etc.
            </p>
            <button
              class="button"
              onClick={() => setShowModal(true)}
            >
              Contact Us
            </button>
            {showModal ? (
            <>
              <Modal
                showModal={showModal}
                onClose={() => setShowModal(false)}
              />
            </>
          ) : null}
            </div>
          </Grid>
        </Grid>
      </Grid>

      
    </div>
  );
}

export default HeroSection;
